.feed-form {
    margin-top: 36px;
    display: flex;
    flex-direction: column;
    width: 300px;

    &__input {
        height: 52px;
        background: #FFFFFF;
        border-radius: 5px;
        margin-bottom: 15px;
        border: none;
        padding: 0 20px;
        font-size: 14px;
        color: #4B4B4B;

        &:nth-child(3) {
            margin-bottom: 20px;
        }

    }
}

@media (max-width: 768px) {
    .feed-form {
        margin: 30px auto;
    }
}

@media (max-width: 425px) {
    .feed-form {
        width: 200px;
        &__input {
            font-size: 11px;
            height: 45px;
        }

    }
}